<template>

  <v-form ref="btnForm">
    <v-card class="dialog_card">
      <v-card-title class="card_title"
                    :style="{width: dialogWidth}">
        <div>{{dialogTitle}}</div>
        <div class="close_dialog"
             @click="cancel">X</div>
      </v-card-title>
      <v-card-text class="dialog_content">
        <v-row dense>
          <v-col class="d-flex"
                 cols="12"
                 sm="2">
            <v-switch :label="detailData.status_uuid? $t('label_To_enable_the') : $t('label_disable')"
                      color="primary"
                      :readonly="onlyShow"
                      v-model="detailData.status_uuid"
                      true-value='1'
                      false-value='0'></v-switch>
          </v-col>
        </v-row>
        <v-row dense>
          <v-col class="d-flex"
                 cols="12"
                 sm="12">
            <v-select :items="btnType"
                      :readonly="onlyShow"
                      :label="$t('label_type') + '*'"
                      v-model="detailData.button_type"
                      item-text='dict_label'
                      item-value='dict_value'
                      required
                      chips
                      :rules="[v => !!v || '类型']"></v-select>
          </v-col>
          <v-col class="d-flex"
                 cols="12"
                 sm="12">
            <v-select :items="btnFunc"
                      :readonly="onlyShow"
                      v-model="detailData.button_category_uuid"
                      :label="$t('label_function') + '*'"
                      item-text='dict_label'
                      item-value='dict_value'
                      required
                      chips
                      :rules="[v => !!v || $t('label_function')]"></v-select>
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <mrc_ess_multiText required
                               style="width: 100%"
                               :label="$t('label_The_name_of_the_button') + '*'"
                               :readonly="onlyShow"
                               :isNational="judegNational('button_name')"
                               v-model="detailData.button_name"
                               append-icon="mdi-longitude"
                               :rules="$text_rules($t('label_The_name_of_the_button'))">
            </mrc_ess_multiText>
            <!-- <v-text-field required
                          :label="$t('label_The_name_of_the_button') + '*'"
                          :readonly="onlyShow"
                          v-model="detailData.button_name"
                          append-icon="mdi-longitude"
                          :rules="$text_rules('按钮名称')">
            </v-text-field> -->
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <!-- <v-text-field required
                          :label="$t('label_The_title') + '*'"
                          :readonly="onlyShow"
                          v-model="detailData.button_title"
                          append-icon="mdi-longitude"
                          :rules="$text_rules('标题')">
            </v-text-field> -->
            <mrc_ess_multiText required
                               style="width: 100%"
                               :label="$t('label_The_title') + '*'"
                               :isNational="judegNational('button_title')"
                               :readonly="onlyShow"
                               v-model="detailData.button_title"
                               :rules="$text_rules($t('label_The_title'))">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <mrc_ess_multiText required
                               style="width: 100%"
                               :label="$t('label_Button_description') + '*'"
                               :isNational="judegNational('button_description')"
                               :readonly="onlyShow"
                               v-model="detailData.button_description"
                               :rules="$text_rules($t('label_Button_description'))">
            </mrc_ess_multiText>
            <!-- <v-text-field required
                          :label="$t('label_Button_description') + '*'"
                          :readonly="onlyShow"
                          v-model="detailData.button_description"
                          append-icon="mdi-longitude"
                          :rules="$text_rules('按钮描述')">
            </v-text-field> -->
          </v-col>
          <v-col cols="12"
                 sm="4"
                 class="d-flex">
            <v-checkbox color="primary"
                        :readonly="onlyShow"
                        :label="$t('label_Function_entry') + '*'"
                        v-model="detailData.is_main"
                        :true-value='1'
                        :false-value='0'></v-checkbox>
          </v-col>
          <v-col cols="12"
                 sm="4"
                 class="d-flex">
            <v-checkbox color="primary"
                        :readonly="onlyShow"
                        :label="$t('label_send_email') + '*'"
                        v-model="detailData.is_send_email"
                        :true-value='1'
                        :false-value='0'></v-checkbox>
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <v-text-field :label="$t('label_URL')"
                          :readonly="onlyShow"
                          v-model="detailData.url"
                          >
            </v-text-field>
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <v-text-field :readonly="onlyShow"
                          :label="$t('label_components')"
                          v-model="detailData.button_component"
                          required
                          >
            </v-text-field>
          </v-col>
          <v-col class="d-flex"
                 cols="12"
                 sm="12">
            <v-select :items="client_type"
                      :label="$t('label_Client_type') + '*'"
                      :readonly="onlyShow"
                      item-value="value_field"
                      item-text="view_fields"
                      v-model="detailData.client_type_uuid"
                      required
                      multiple
                      chips
                      :rules="[v => v.length>0 || $t('label_Client_type')]"></v-select>
          </v-col>
          <v-col cols="12"
                 sm="12">
            <!-- required -->
            <!-- 修改的时候新增的可选 -->
            <v-select :items="version_arr"
                      v-model="detailData.version"
                      :label="$t('label_version') + '*'"
                      v-if="update_add && !detailData.uuid"
                      :readonly="onlyShow"
                      :rules="[v=>!!v ||$t('label_version')]"
                      chips></v-select>
            <!-- 修改的时候 之前的按钮  只读版本 -->
            <v-text-field v-if='detailData.uuid'
                          v-model="detailData.version"
                          :label="$t('label_version') + '*'"
                          readonly></v-text-field>
            <!-- 新增的时候版本和功能版本一致 -->
            <v-text-field v-if="!detailData.uuid && !update_add"
                          v-model="version"
                          :label="$t('label_version') + '*'"
                          readonly></v-text-field>
          </v-col>
          <v-col cols="12"
                 sm="12"
                 class="d-flex">
            <v-text-field :label="$t('label_Button_icon') + '*'"
                          :readonly="onlyShow"
                          v-model="detailData.button_icon">
            </v-text-field>
          </v-col>
          <v-col class="d-flex"
                 cols="12"
                 sm="12">
            <!-- v-model.number="" -->
            <v-text-field @blur='verifyNum'
                          :label="$t('label_The_sorting') + '*'"
                          :readonly="onlyShow"
                          v-model="detailData.sequence"
                          :rules="$text_rules($t('label_The_sorting'),[ v => /^[1-9]*$/.test(v) || '请输入数字'])"></v-text-field>
          </v-col>
        </v-row>
      </v-card-text>

      <mrc_ess_submitBtn v-if="!onlyShow"
                         @cancel='cancel'
                         @success='success'
                         :successText="$t('label_save')"></mrc_ess_submitBtn>
    </v-card>
  </v-form>
</template>

<script>
import { getDataByName } from '@/service/api/data_dictionary'
import { get_versions } from '@/service/api/func_module'
export default {
  components: {},
  props: {
    dialogTitle: {
      type: String
    },
    dialogWidth: {
      type: String
    },
    client_type: {
      type: Array
    },
    btnFunc: {
      type: Array
    },
    btnType: {
      type: Array
    },
    version_arr: {
      type: Array
    },
    version: {
      type: String
    },
    dialogDefaultData: {
      type: Object
    },
    onlyShow: {
      type: Boolean
    },
    update_add: {
      type: Boolean
    }
  },
  data () {
    return {
      detailData: {
        button_category_uuid: "", //  按钮功用
        client_type_uuid: [], // 客户端类型
        button_type: '',  // 按钮类型
        button_name: "", // 按钮名称
        button_description: "", // 按钮描述
        is_main: 0,          // 功能入口  1 是 0 否
        status_uuid: 0,         // 启用  1 是 0 否
        button_title: "", // 按钮标题
        url: "",
        button_icon: '', // 按钮图标
        sequence: 1, // 排序
        is_send_email: 0,   // 发送邮件  1是 0 否
        button_component: '',  // component
        version: '' // 版本
      },
      checkbox: true,
      checkbox1: true,
      uuid: ''
    };
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_function_buttons')
    this.dataShow(this.dialogDefaultData)
  },
  methods: {
    // 弹窗数据回显
    dataShow (obj) {
      // 往弹窗传值才做下面的操作
      if (!Object.keys(obj).length) {
        return
      }
      const data = this.$clone(obj)
      obj.button_type = String(obj.button_type)
      if (data.client_type_uuid) {
        data.client_type_uuid = data.client_type_uuid.split(',')
      }
      this.detailData = data
    },
    // 关闭dialog 不做修改
    cancel () {
      this.reset()
      this.$emit('cancel')
    },
    reset () {
      this.detailData = {
        button_category_uuid: "", //  按钮功用
        client_type_uuid: [], // 客户端类型
        button_type: '',  // 按钮类型
        button_name: "", // 按钮名称
        button_description: "", // 按钮描述
        is_main: 0,          // 功能入口  1 是 0 否
        status_uuid: 0,         // 启用  1 是 0 否
        button_title: "", // 按钮标题
        url: "",
        button_icon: '', // 按钮图标
        sequence: 1, // 排序
        is_send_email: 0,   // 发送邮件  1是 0 否
        button_component: '',  // component
        version: '' // 版本
      }
    },
    // 保存
    success () {
      const isSubmit = this.$refs.btnForm.validate()
      if (!isSubmit) {
        return
      }
      if (this.version_arr.length <= 0) {
        this.detailData.version = this.version
      }
      const obj = this.$clone(this.detailData)
      obj.client_type_uuid = obj.client_type_uuid.join()
      this.$emit('getBtnDetail', obj)
      this.reset()
    },
    verifyNum (e) {
      if (!/^[1-9]*$/.test(Number(e.target.value))) {
        this.detailData.sequence = 1
        e.target.value = 1
      }
    },
  },
  computed: {
  },
  watch: {
    dialogDefaultData: {
      handler (val) {
        this.dataShow(val)
      },
      deep: true
    }
  },
};
</script>

<style scoped>
.dialog_card {
  overflow: hidden;
}
.card_title {
  padding: 6px 0 6px 10px !important;
  display: flex;
  justify-content: space-between;
  font-size: 14px !important;
  color: #fff;
  background-color: #000000;
}
.close_dialog {
  width: 32px;
  text-align: center;
  cursor: pointer;
}
</style>
