<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div class="add_func_btn">
      <mrc_ess_iconGroup style="top: -50px"
                         v-show="!disabled"
                         v-if='$store.state.uuid'
                         @handleReturn='handleReturn'
                         :showEdit="hasBtn('edit-button')"
                         @handleEdit='handleEdit'
                         :show_del='false'></mrc_ess_iconGroup>
      <v-card>
        <v-card-text>
          <v-data-table :headers="header"
                        :items="desserts"
                        class="elevation-1"
                        hide-default-footer
                        @click:row='changeItem($event, true)'>

            <template v-slot:item.button_type='{ item }'>
              {{btnTypeToName(item.button_type)}}
            </template>

            <template v-slot:item.status_uuid='{ item }'>
              <div class="status"
                   :class="{'is_used': item.status_uuid==0}">{{item.status_uuid==1 ? $t('label_To_enable_the') : $t('label_disable')}}</div>
            </template>
            <template v-slot:item.actions='{item}'>
              <v-icon @click.stop="changeItem(item, false)">mdi-pencil</v-icon>
              <v-icon @click.stop="delItem(item)"
                      v-if="hasBtn('edit-button')">mdi-delete</v-icon>
              <v-icon v-show='item.uuid'
                      @click.stop="upItem(item)">mdi-sort-descending</v-icon>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-dialog v-model="dialog"
                    :width="dialogWidth"
                    scrollable
                    :persistent='!onlyShow'>
            <template v-slot:activator="{ on, attrs }">

              <v-btn text
                     v-bind="attrs"
                     v-on="on"
                     v-show="disabled"
                     color="primary"
                     @click="open_btn_dialog">
                <span>{{$t('label_Add_a_button')}}</span>
                <v-icon class="add_btn">mdi-close</v-icon>
              </v-btn>
            </template>
            <!-- :func_version='version' -->
            <!-- :version_uuid='version_uuid' -->
            <add-dialog :dialogWidth='dialogWidth'
                        @getBtnDetail='getBtnDetail'
                        @cancel='dialogClose'
                        :client_type='client_type'
                        :btnFunc='btnFunc'
                        :dialogTitle='dialogTitle'
                        :btnType='btnType'
                        :version_arr='version_arr'
                        :version='func_version'
                        :dialogDefaultData='dialogDefaultData'
                        :onlyShow='onlyShow'
                        :update_add='update_add'></add-dialog>
          </v-dialog>
        </v-card-actions>
        <v-card-actions>
          <mrc_ess_submitBtn v-show="disabled"
                             :cancelText='cancelText'
                             :successText='successText'
                             :loading='submitLoading'
                             @cancel='cancel'
                             @success='success'></mrc_ess_submitBtn>
        </v-card-actions>
      </v-card>
    </div>
  </mrc_ess_skLoaders>
</template>

<script>
import addDialog from './add_btn_dialog';
import { getDataByName } from '@/service/api/data_dictionary'
import { get_versions, up_versions, update_buttons, get_func_update } from '@/service/api/func_module'
// import func from '../../../../vue-temp/vue-editor-bridge';
export default {
  components: { addDialog },
  props: {
    client_type: {
      type: Array
    },
    version: {
      type: String
    },
    version_uuid: {
      type: String
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      dialogTitle: '',
      submitLoading: false,
      delDesserts: [],
      skLoading: true,
      cancelText: this.$t('label_The_previous_step'),
      successText: this.$t('label_submit'),
      dialogWidth: '600px',
      desserts: [],
      dialog: false,
      btnFunc: [], // 按钮功用
      btnType: [], // 按钮类型
      version_arr: [],
      dialogDefaultData: {},
      editedIndex: -1,
      onlyShow: false,
      update_add: false,
      disabled: false,
      func_version: '',
      showActions: false
    };
  },
  created () { },
  async mounted () {
    this.skLoading = true
    try {
      this.func_version = this.version
      // const version_data = await get_versions({version: this.func_version,module_uuid: this.version_uuid})
      // this.version_arr = version_data.data.version.split(',')
      // 按钮类型普通参照
      const btnType = await getDataByName('buttonType')
      btnType.code == 200 ? this.btnType = btnType.data : this.btnType = []
      // 按钮功用 普通参照
      const btnFunc = await getDataByName('buttonUse')
      btnFunc.code == 200 ? this.btnFunc = btnFunc.data : this.btnFunc = []
      this.desserts = this.$clone(this.list)
      if (this.$store.state.uuid) {
        this.cancelText = '取消'
      } else {
        this.showActions = true
        // const actions = {
        //   text: ' ',
        //   align: 'start',
        //   sortable: false,
        //   value: 'actions',
        //   width: '120px'
        // }
        // this.header.push(actions)
        this.disabled = !this.disabled
      }
      this.skLoading = false
    } catch (error) {
      this.skLoading = false
    }

  },
  methods: {
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
      this.showActions = true
      // const actions = {
      //   text: ' ',
      //   align: 'start',
      //   sortable: false,
      //   value: 'actions',
      //   width: '120px'
      // }
      // this.header.push(actions)
    },
    // 添加按钮
    async open_btn_dialog () {
      this.dialog = true
      this.dialogTitle = '添加按钮'
      this.onlyShow = false
      // 就只有修改功能的地方  新增按钮才会修改版本 传个属性过去
      if (this.$store.state.uuid) {
        const version_data = await get_versions({ version: this.version, module_uuid: this.version_uuid })
        this.version_arr = version_data.data.version.split(',')
        this.update_add = true
      }
    },
    // 保存
    async getBtnDetail (obj) {
      if (this.editedIndex > -1) {
        // obj.execute = 2 // execute 1 增 2 改 3 删
        Object.assign(this.desserts[this.editedIndex], obj)
      } else {
        // obj.execute = 1
        this.desserts.push(obj)
      }
      // const update = await update_buttons(obj)
      // if (update.code == 200) {
      //   this.$store.commit('newsTips', this.$t('message_The_submission_was_successful'))
      //   this.disabled = !this.disabled
      // }
      this.dialogClose()
    },
    dialogClose () {
      this.dialog = false
      this.editedIndex = -1
      this.dialog = false
      this.update_add = false
    },
    // 取消 上一步
    cancel () {
      if (this.$store.state.uuid) {
        this.disabled = !this.disabled
        this.showActions = false
        // this.header.pop()
      } else {
        this.$emit('cancel')
      }
    },
    // 提交按钮
    async success () {
      // 新增 功能按钮提交this.$store.state.uuid
      const hasClient = this.desserts.every(item => {
        return item.client_type_uuid != ''
      })
      if (!hasClient) {
        this.$store.commit('newsTips', this.$t('message_Function_client'))
        return
      }
      if (!this.$store.state.uuid) {
        this.$emit('success', this.desserts)
      } else {
        // 新增按钮提交
        let arr = this.$clone(this.desserts)
        arr = arr.concat(this.delDesserts)
        arr.forEach(item => {
          if (item.button_id) {
            delete item.button_id
          }
        })
        this.submitLoading = true
        const update = await update_buttons({
          uuid: this.$store.state.uuid,
          function_name: this.$route.meta.title,
          mrbase_sys_function_buttons: arr,
          // created_utc_datetime: 1
        })
        if (update.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_submission_was_successful'))
          this.header.pop()
          this.disabled = !this.disabled
          this.$router.go(-1)
        }
        this.submitLoading = false
      }

    },
    changeItem (item, onlyShow) {
      this.onlyShow = onlyShow
      if (!item.uuid) {
        this.update_add = true
        // item.execute = 1
      } else {
        // item.execute = 2
      }
      this.editedIndex = this.desserts.indexOf(item)
      this.dialogDefaultData = this.$clone(item)
      this.dialogTitle = this.dialogDefaultData.button_name
      this.dialog = true

    },
    delItem (item) {
      const index = this.desserts.indexOf(item)
      // confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
      if (item.uuid) {
        // item.execute = 3
        item['~type~'] = 'del'
        this.delDesserts.push(item)
      }
      this.desserts.splice(index, 1)
    },
    // 升级版本
    async upItem (item) {
      const index = this.desserts.indexOf(item)
      const up = await up_versions({ uuid: item.uuid })
      if (up.code == 200) {
        this.$store.commit('newsTips', this.$t('message_update_successed'))
        // this.disabled = !this.disabled
        // this.header.pop()
        item.version = up.data.version
        this.$set(this.desserts, index, item)
        this.func_version = up.data.version
      }
    }
  },
  computed: {
    btnTypeToName (val) {
      return (val) => {
        let name = ''
        this.btnType.forEach(item => {
          if (item.dict_value == val) {
            name = item.dict_label
          }
        })
        return name
      }
    },
    header () {
      const arr = [
        {
          text: this.$t('label_type'),
          align: 'start',
          sortable: false,
          value: 'button_type',
        },
        {
          text: this.$t('label_The_name_of_the_button'),
          align: 'start',
          sortable: false,
          value: 'button_name',
        },
        {
          text: this.$t('label_Client_type'),
          align: 'start',
          sortable: false,
          value: 'client_type_uuid',
        },
        {
          text: this.$t('label_The_title'),
          align: 'start',
          sortable: false,
          value: 'button_title',
        },
        {
          text: this.$t('label_URL'),
          align: 'start',
          sortable: false,
          value: 'url',
        },
        {
          text: this.$t('label_state'),
          align: 'start',
          sortable: false,
          value: 'status_uuid',
        },

      ]
      if (this.showActions) {
        arr.push({
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions',
          width: '120px'
        })
      }
      return arr
    }
  },
  watch: {
    async version (val) {
      if (this.$store.state.uuid) {
        const version_data = await get_versions({ version: val, module_uuid: this.version_uuid })
        this.version_arr = version_data.data.version.split(',')
      } else {
        this.func_version = val
        // 新增功能的时候新增按钮 按钮的版本和功能的版本一致 
        this.desserts.length > 0 && this.desserts.forEach(item => {
          item.version = val
        })
      }

    },
    list: {
      handler (val) {
        this.desserts = val
      },
      deep: true
    },
    client_type: {
      handler (val, old) {
        // const ischange = val.map(item => {
        //   return old.every(i => {
        //     return item.value_field == i.value_field
        //   })
        // })
        // 如果上一次修改的客户端列表在新的客户端列表里面一个都没有 那就把按钮列表的客户端列表字段置为空，重新选择客户端列表
        this.desserts.forEach(i => {
          val.forEach(j => {
            if (j.value_field.indexOf(i.client_type_uuid) <= -1) {
              i.client_type_uuid = ''
            }
          })
        })
      },
      deep: true
    }
  },
};
</script>

<style scoped>
.add_func_btn .add_btn {
  transform: rotateZ(45deg);
  font-size: 17px;
}
</style>
